.cat {
  position: relative;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  &:hover {
    transform: scale(1.1);
    box-shadow: 0px 6px 5px rgba(0, 0, 0, 0.24),
      0px 9px 18px rgba(0, 0, 0, 0.18);
    .button {
      opacity: 1;
      visibility: visible;
    }
  }
}

.image {
  object-fit: cover;
  width: 100%;
  height: 225px;
}

.button {
  position: absolute;
  display: block;
  right: 20px;
  bottom: 25px;
  width: 40px;
  height: 37px;
  background-image: $btnRegular;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0;
  visibility: hidden;
  transition: background-image 0.2s ease, opacity 0.2s ease,
    visibility 0.2s ease;
  &:hover {
    background-image: $btnHovered;
  }
  &:active,
  &_active {
    background-image: $btnActive;
  }
  &_active:hover {
    background-image: $btnActive;
  }
}

@media only screen and (max-width: 1024px) {
  .cat {
    transition: none;
    &:hover {
      transform: scale(1);
      box-shadow: none;
    }
  }

  .button {
    opacity: 1;
    visibility: visible;
  }
}
